<template>
  <!-- top10 -->
  <div class="box">
    <div class="charts" ref="chart">

    </div>
  </div>
</template>
<script>


export default {
  name: 'timeer',
  data() {
    return {
      chartInstance: null,
      list: null
    }
  },
  mounted() {
    this.initChart()
    this.getData()
  },
  methods: {
    // echartsInstance
    initChart() {
      this.chartInstance = this.$echarts.init(this.$refs.chart)
    },
    // 
    getData() {
      this.updateData()
    },
    // 
    updateData() {
      const option = {
        title: {
          text: 'top10',
          left: 'center',
          padding: [15, 0, 10, 0],
          textStyle: {
            color: '#FFFFFF'
          },
        },
        // 
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#283b56'
            }
          }
        },
        // 
        legend: {
          x: 'center',
          y: 'bottom',
          textStyle: {
            color: '#FFFFFF'
          },
          itemHeight: 8,
          itemGap: 10,
          icon: 'roundRect',
          data: ['()', '']
        },
        xAxis: {
          type: 'value',
          splitLine: {
            show: false
          },
          axisLine: {
            show: false
          },
          axisLabel: {
            color: '#FFFFFF'
          }
        },
        yAxis: {
          type: 'category',
          data: ['A', 'B', 'C', 'D', 'E', 'F', 'G'],
          axisLabel: {
            color: '#FFFFFF'
          },
          axisLine: {
            lineStyle: {
              color: '#FFFFFF',
            }
          },
          axisTick: {
            alignWithLabel: true,
            show: false
          },
        },
        series: [
          {
            type: 'bar',
            data: ['19', '23', '48', '30', '40', '56', '89'],
            name:'()',
            showSymbol:false,
            color:'#5B9BD5'
          },
          {
            type: 'bar',
            data: ['10', '23', '18', '10', '7', '8', '9'],
            name:'',
            showSymbol:false,
            color:'#EB7D33'
          },
        ],
        // color: '#5B9BD5'
      }
      this.chartInstance.setOption(option)
    }
  }
}
</script>
