<template>
  <!--  -->
  <div class="box">
    <div class="charts" ref="chart">

    </div>
  </div>
</template>
<script>


export default {
  name: 'violation',
  data() {
    return {
      chartInstance: null,
      data: null,
      reason: ['', '', '', ''],
      pieList: null,
    }
  },
  created() {
    var formID = this.GetRequest("formID");
    // yigolist
    this.list = window.parent.exec(formID, "DBNamedQuery('StatusQuoOfViolations')");
    console.log('this.list', this.list);
    // 
    this.data = this.list.allRows[0].vals.map(el => {
      return el.c[0]
    })
    console.log("this.data", this.data);
  },
  mounted() {
    this.initChart()
    this.getData()
  },
  methods: {
    // yigo
    GetRequest(name) {
      var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
      var r = window.location.search.substr(1).match(reg);
      if (r != null) {
        return decodeURIComponent(r[2]);
      }
      else {
        return null;
      }
    },
    // echartsInstance
    initChart() {
      this.chartInstance = this.$echarts.init(this.$refs.chart)
    },
    // 
    getData() {
      // 
      this.pieList = [];
      let arr =[]
      this.data.forEach((el,index) => {
        if(0<index&&index<5){
          arr.push(el)
        }
      });
      console.log(arr);
      for (var i = 0; i < this.data.length; i++) {
          var d = new Object();
          d.name = this.reason[i];
          d.value =arr[i];
          this.pieList.push(d)
      }
      console.log('this.pieList', this.pieList);
      this.updateData()
    },
    // 
    updateData() {
      const option = {
        title: {
          text: '',
          left: 'center',
          padding: [15, 0, 10, 0],
          textStyle: {
            color: '#FFFFFF'
          },
        },
        tooltip: {
          trigger: 'item',
          formatter: '{b}:{d}%'
        },
        legend: {
          orient: 'vertical',
          left: 'right',
          y: 'center',
          textStyle: {
            color: '#FFFFFF'
          }
        },
        color: ['#A5A5A5', '#FFC000', '#5B9BD5', '#ED7D31'],
        series: [
          {
            type: 'pie',
            radius: '50%',
            data: this.pieList,
            label: {
              normal: {
                show: true,
                position: 'inner',
                formatter: '{d}%',
                textStyle: {
                  fontWeight: 300,
                  fontSize: 16    //
                },
              }
            },
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }
      this.chartInstance.setOption(option)
    }
  }
}
</script>
