<template>
  <!--  -->
  <div class="box">
    <div class="charts" ref="radarchart">

    </div>
  </div>
</template>
<script>


export default {
  name: 'onereview',
  data() {
    return {
      chartInstance: null,
      list: null,
      xdata: null,
      ydataone: null,
      ydatatwo: null,
    }
  },
  // created() {
  //   var formID = this.GetRequest("formID");
  //   // yigolist
  //   this.list = window.parent.exec(formID, "DBNamedQuery('')");
  //   console.log('this.list', this.list);
  //   // X,Y
  //   this.xdata = this.list.allRows.map(el => {
  //     return el.vals[0]
  //   })
  //   this.ydataone = this.list.allRows.map(el => {
  //     return el.vals[1].c[0]
  //   })
  //   this.ydatatwo = this.list.allRows.map(el => {
  //     return el.vals[2].c[0]
  //   })
  // },
  mounted() {
    this.initChart()
    this.getData()
  },
  methods: {
    // yigo
    GetRequest(name) {
      var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
      var r = window.location.search.substr(1).match(reg);
      if (r != null) {
        return decodeURIComponent(r[2]);
      }
      else {
        return null;
      }
    },
    // echartsInstance
    initChart() {
      this.chartInstance = this.$echarts.init(this.$refs.radarchart)
    },
    // 
    getData() {
      this.updateData()
    },
    // 
    updateData() {
      const option = {
        title: {
          text: '',
          left: 'center',
          padding: [15, 0, 10, 0],
          textStyle: {
            color: '#FFFFFF',
          },

        },
        // 
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#283b56'
            }
          }
        },
        // 
        legend: {
          x: 'center',
          y: 'bottom',
          textStyle: {
            color: '#FFFFFF'
          },
          itemHeight: 8,
          itemGap: 10,
          icon: 'roundRect',
          data: ['', '']
        },
        xAxis: {
          type: 'category',
          data: ['1', '2', '3'],
          axisLabel: {
            color: "#FFFFFF",
          },
          axisLine: {
            lineStyle: {
              color: '#FFFFFF',
            }
          },
          axisTick: {
            alignWithLabel: true,
            show: false
          },
        },
        yAxis: [
          {
            type: 'value',
            //   name: '',
            axisLine: {
              show: false
            },
            splitLine: {
              show: false
            },
            axisLabel: {
              color: '#FFFFFF'
            },
            axisTick: {
              alignWithLabel: true,
              show: false
            },
            max: 90,
            min: 0,
          },
        ],
        series: [{
          name: '',
          data: [80, 92, 78],
          type: 'bar',
          showSymbol: false,
          color: '#EB7D33',

        }, {
          name: '',
          data: [18, 10, 22],
          type: 'bar',
          showSymbol: false,
          color: '#5B9BD5'
        }
        ]
      }
      this.chartInstance.setOption(option)
      // setTimeout(function () {
      //   window.onresize = function () {
      //     twChart.resize();
      //   }
      // }, 200)
    }
  }
}
</script>
