<template>
  <!--  -->
  <div class="box">
    <div class="charts" ref="chart">

    </div>
  </div>
</template>
<script>


export default {
  name: 'absent',
  data() {
    return {
      chartInstance: null,
      list: null,
      xdata: null,
      ydata: null,
      pieList: null,
    }
  },
  created() {
    var formID = this.GetRequest("formID");
    // yigolist
    this.list = window.parent.exec(formID, "DBNamedQuery('AbsenceOfJudges')");
    console.log('this.list', this.list);
    // X,Y
    this.xdata = this.list.allRows.map(el => {
      return el.vals[1]
    })
    this.ydata = this.list.allRows.map(el => {
      return el.vals[2].c[0]
    })

  },
  mounted() {
    this.initChart()
    this.getData()
  },
  methods: {
    // yigo
    GetRequest(name) {
      var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
      var r = window.location.search.substr(1).match(reg);
      if (r != null) {
        return decodeURIComponent(r[2]);
      }
      else {
        return null;
      }
    },
    // echartsInstance
    initChart() {
      this.chartInstance = this.$echarts.init(this.$refs.chart)
    },
    // 
    getData() {
      // 
      this.pieList = [];
      for (let i = 0; i < this.xdata.length; i++) {
        var d = new Object();
        d.name = this.xdata[i];
        d.value = this.ydata[i];
        this.pieList.push(d)
      }
      this.updateData()
    },
    // 
    updateData() {
      const option = {
        title: {
          text: '',
          left: 'center',
          padding: [15, 0, 10, 0],
          textStyle: {
            color: '#FFFFFF'
          },
        },
        tooltip: {
          trigger: 'item',
          formatter: '{b}:{d}%'
        },
        legend: {
          orient: 'vertical',
          left: 'right',
          y: 'center',
          textStyle: {
            color: '#FFFFFF'
          }
        },
        color: ['#A5A5A5', '#FFC000', '#5B9BD5', '#ED7D31'],
        series: [
          {
            type: 'pie',
            data: this.pieList,
            radius: '50%',
            label: {
              normal: {
                show: true,
                position: 'inner',
                formatter: '{d}%',
                textStyle: {
                  fontWeight: 300,
                  fontSize: 16    //
                },
              }
            },
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }
      this.chartInstance.setOption(option)
    }
  }
}
</script>
